.header{
  min-height: 60vh; 
  width: 100%;
  background-image: linear-gradient(rgba(182, 187, 205, 0.7), rgba(4,9,30,0.7)), url("./HomePageimages/CUHK.jpeg");
  background-color: #5d4e99;
  background-position: center;
  background-size: cover;
  position: relative;
}

nav{
  display: flex;
  padding: 2% 6%;
  justify-content: space-between;
  align-items: center;
}

nav img{
  width: 150px;
  height: 150px;
}

.nav-links{
  flex: 1;
  text-align: right;
}

.nav-links ul li{
  list-style: none;
  display: inline-block;
  padding: 8px 12px;
  position: relative;
}

.nav-links ul li a{
  color: #fff;
  text-decoration: none;
  font-size: 13px;
}

.nav-links ul li::after{
  content: '';
  width: 100%;
  height: 2px;
  background: #5d4e99;
  display: block;
  margin: auto;
  transition:  0.5s;
}

.nav-links ul li:hover::after{
  width: 0%;
}

.text-box{
  width: 90%;
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.text-box h1{
  font-size: 50px;
}

.text-box p{
  margin: 10px 0 40px;
  font-size: 14px;
  color: #fff
}


.hero-btn{
  display: inline-block;
  text-decoration: none;
  color: #fff;
  border: 1px solid #fff;
  padding: 12px 34px;
  font-size: 13px;
  background: transparent;
  position: relative;
  cursor: pointer;
}

.hero-btn:hover{
  border: 1px solid #5d4e99;
  background: #5d4e99;
  transition: 1s;
}

nav .fa{
  display: none;

}

@media(max-width: 700px){
  .text-box h1{
      font-size: 62px;
  }
  .nav-links ul li{
      display: block;
  }
  .nav-links{
      position: absolute;
      background: #5d4e99;
      height: 100vh;
      width: 200px;
      top: 0;
      right: -200px;
      text-align: left;
      z-index: 2;
      transition: 1s;
  }
  nav .fa{
      display: block;
      color: #fff;
      margin: 10px;
      font-size: 22px;
      cursor: pointer;
  }
  .nav-links ul{
      padding: 30px;
  }
}



/* -----restaurant ----- */
.restaurant{
  width: 80%;
  margin: auto;;
  text-align: center;
  padding-top: 100px;
}

h1{
  font-size: 36px;
  font-weight: 600;
}

p{
  color: #777;
  font-size: 14px;
  font-weight: 300;
  line-height: 22px;
  padding: 10px;
}

.row{
  margin-top: 5%;
  display: flex;
  justify-content: space-between;
}

.restaurant-col{
  flex-basis: 31%;
  background: #fff3f3;
  border-radius: 10px;
  margin-bottom: 5%;
  padding: 20px 12px;
  box-sizing: border-box;
}

h3{
  text-align: center;
  font-weight: 600;
  margin: 10px 0;
}

.restaurant-col:hover{
  box-shadow: 0 0 20px 0px rgba(0,0,0,0.2);
}

@media(max-width: 700px){
  .row{
      flex-direction: column;
  }
}

/* ----Dishes---- */
.Dishes{
  width: 80%;
  margin: auto;
  text-align: center;
  padding-top: 50px;
}
.dishes-col{
  flex-basis: 32%;
  border-radius: 10px;
  margin-bottom: 30px;
  position: relative;
  overflow: hidden;
}
.dishes-col img{
  width: 100%;
  display: block;
}
.layer{
  background: transparent;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transition: 0.5s;
}
.layer:hover{
  background: rgba(50, 39, 245, 0.54)
}
.layer h3{
  width: 100%;
  font-weight: 500;
  color: #fff;
  font-size: 26px;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  opacity: 0;
  transition: 0.5s;
}
.layer:hover h3{
  bottom: 49%;
  opacity: 1;
}

/* -----Location----- */
.Location{
  width: 80%;
  margin: auto;
  text-align: center;
  padding-top: 100px;
}
.location-col{
  flex-basis: 31%;
  border-radius: 10px;
  margin-bottom: 5%;
  text-align: left;
}
.location-col img{
  width: 100%;
  border-radius: 10px;
}
.location-col p{
  padding: 0;
}
.location-col h3{
  margin-top: 16px;
  margin-bottom: 15px;
  text-align: left;
}

.location-col-inmenu {
  flex-basis: 31%;
  border-radius: 10px;
  margin-bottom: 5%;
  text-align: center;
}

.location-col-inmenu img{
  width: 50%;
  border-radius: 10px;
}
